.gpt3__whatgpt3 {
    background: var(--color-footer);
    padding: 2rem;
    background: radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));

}

.gpt3__whatgpt3-feature {
    display: flex;
}

.gpt3__whatgpt3-feature .gpt3__whatgpt3-features-container__feature {
    margin: 0;
}

.gpt3__whatgpt3-feature .gpt3__whatgpt3-features-container__feature-text {
    max-width: 700px;
}

.gpt3__whatgpt3-heading {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 4rem 0 2rem;
}

.gpt3__whatgpt3-heading h1 {
    font-size: 34px;
    font-family: var(--font-family);
    font-weight: 800;
    max-width: 510px;
    line-height: 45px;
}

.gpt3__whatgpt3-heading p {
    font-family: var(--font-family);
    color: var(--color-subtext);
    font-size: 16px;
    font-weight: 500;
    line-height: 30px;
    cursor: pointer;
}

.gpt3__whatgpt3-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-top: 2rem;
}


@media screen and (max-width: 850px) {
    .gpt3__whatgpt3-heading {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 0rem;
    }

    .gpt3__whatgpt3-heading p {
        margin-top: 1rem;
    }
}

@media screen and (max-width: 650px) {

    /* Customizing feature component as per needs */
    .gpt3__whatgpt3-feature .gpt3__features-container__feature {
        flex-direction: column;
    }

    /* Customizing feature component as per needs */
    .gpt3__whatgpt3-feature .gpt3__features-container_feature-text {
        margin-top: 0.5rem;
    }

    .gpt3__whatgpt3-heading h1 {
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width: 350px) {

    /* Customizing feature component as per needs */
    .gpt3__whatgpt3-container .gpt3__features-container__feature {
        margin: 1rem 0;
        min-width: 100%;
    }
}